import React from 'react';
import './Admin.css';
import myPic from '../Img/my pic.jpg';
import call from '../Icon/call.png';
import email from '../Icon/email.png';
import home from '../Icon/location.png';
import fb from '../Icon/facebook.png';
import whatsApp from '../Icon/iconfinder_social-whatsapp-circle_4017334.png';
import twiter from '../Icon/twitter.png';

const Admin = () => {
    return (
        <div class="container">
            <div>
                <h1 class="Name">
                    MD. Nur Alom
                </h1>
                <div class="about-me">
                    <img class="my-img" src={myPic} alt=""/>
                    <div class="contact">
                        <h5>CONTACT</h5>
                        <hr/>
                        <div class="contact-info">
                            <img src={call} alt=""/>
                            <p>01712325360</p>
                        </div>
                        <div class="contact-info">
                            <img src={email} alt=""/>
                            <p>khandarparaudc@gmail.com</p>
                        </div>
                        <div class="contact-info one">
                            <img src={home} alt=""/>
                            <p>Khandarpara, Muksudpur, Gopalganj</p>
                        </div>

                        <div class="hobbies">
                            <h5>Social Link</h5>
                            <hr/>
                            <div>
                                <a href="0">
                                    <img src={fb} alt=""/>
                                </a>
                                <a href="0">
                                    <img src={whatsApp} alt=""/>
                                </a>
                                <a href="0">
                                    <img src={twiter} alt=""/>
                                </a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div class="login-form">
                <form>
                    <label>Login</label>
                    <br/>
                    <input type="text" placeholder="enter your email"/>
                    <br/>
                    <label>Password</label>
                    <br/>
                    <input type="password" placeholder="enter your password"/>
                    <br/>
                    <br/>
                    <button>Login</button>
                </form>
            </div>
        </div>
    );
};

export default Admin;